export default "import { Component, Inject, OnInit } from \"@angular/core\";\nimport { FormBuilder, FormGroup, Validators } from \"@angular/forms\";\nimport { ToastService } from \"@nova-ui/bits\";\n\n@Component({\n    selector: \"nui-combobox-reactive-form\",\n    templateUrl: \"./combobox-reactive-form.example.component.html\",\n})\nexport class ComboboxReactiveFormExampleComponent implements OnInit {\n    public myForm: FormGroup;\n    public dataset = {\n        items: [\"Item 1\", \"Item 2\", \"Item 3\", \"Item 4\", \"Item 5\"],\n        selectedItem: \"Item 2\",\n    };\n\n    constructor(private formBuilder: FormBuilder,\n                @Inject(ToastService) private toastService: ToastService) {}\n\n    public ngOnInit() {\n        this.myForm = this.formBuilder.group({\n            item: this.formBuilder.control(this.dataset.selectedItem, [Validators.required]),\n        });\n\n        this.myForm.controls[\"item\"].valueChanges.subscribe(value => console.log(value));\n    }\n\n    public onSubmit() {\n        this.myForm.valid ? this.toastService.success({message: \"Your form is valid!\"}) :\n            this.toastService.error({message: `Your form is invalid!`});\n    }\n}\n";